'use client';

import Link from 'next/link';
import { ArrowLeft, Save, Loader2 } from 'lucide-react';

// UI Components
import { Button } from '@/components/ui/button';

// Import Hook & Components
import { useCreatePembelianForm } from '@/hooks/admin/useCreatePembelianForm';
import PembelianHeaderForm from '@/components/admin/pembelian/create/PembelianHeaderForm';
import PembelianItemsForm from '@/components/admin/pembelian/create/PembelianItemsForm';

export default function CreatePembelianPage() {
    const {
        // Data Master
        suppliers,
        payments,
        bahanBakus,
        
        // Form State
        formData,
        setFormData,
        items,
        
        // Calculated
        grandTotal,
        isSaving,

        // Actions
        addItem,
        removeItem,
        updateItem,
        handleSubmit
    } = useCreatePembelianForm();

    return (
        <div className="space-y-6 pb-20 max-w-5xl mx-auto">
            {/* Header Page */}
            <div className="flex items-center gap-4">
                <Link href="/admin/inventaris/pembelian">
                    <Button variant="outline" size="icon"><ArrowLeft className="h-4 w-4" /></Button>
                </Link>
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Catat Pembelian Baru</h1>
                    <p className="text-gray-500 text-sm">Input stok masuk dari supplier.</p>
                </div>
            </div>

            {/* Form Container */}
            <form onSubmit={handleSubmit} className="space-y-6">
                
                {/* 1. Header Faktur */}
                <PembelianHeaderForm 
                    formData={formData}
                    setFormData={(newData: any) => setFormData(prev => ({ ...prev, ...newData }))}
                    suppliers={suppliers}
                    payments={payments}
                />

                {/* 2. Item Barang */}
                <PembelianItemsForm 
                    items={items}
                    addItem={addItem}
                    removeItem={removeItem}
                    updateItem={updateItem}
                    bahanBakus={bahanBakus}
                    grandTotal={grandTotal}
                />

                {/* 3. Tombol Submit */}
                <div className="flex justify-end">
                    <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b] w-full md:w-auto px-8 py-6 text-lg shadow-lg">
                        {isSaving ? <Loader2 className="h-5 w-5 animate-spin mr-2" /> : <Save className="h-5 w-5 mr-2" />}
                        Simpan Transaksi
                    </Button>
                </div>
            </form>
        </div>
    );
}