'use client';

import { Plus, Trash2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import {
    Select, SelectContent, SelectItem, SelectTrigger, SelectValue,
} from "@/components/ui/select";
import { BahanBaku } from '@/types';

interface PembelianItemsFormProps {
    items: any[];
    addItem: () => void;
    removeItem: (index: number) => void;
    updateItem: (index: number, field: string, value: string) => void;
    bahanBakus: BahanBaku[];
    grandTotal: number;
}

export default function PembelianItemsForm({
    items,
    addItem,
    removeItem,
    updateItem,
    bahanBakus,
    grandTotal
}: PembelianItemsFormProps) {
    
    return (
        <Card>
            <CardHeader className="flex flex-row justify-between items-center pb-3 border-b mb-3">
                <CardTitle className="text-base">Daftar Barang</CardTitle>
                <Button type="button" variant="secondary" size="sm" onClick={addItem}>
                    <Plus className="h-4 w-4 mr-2" /> Tambah Baris
                </Button>
            </CardHeader>
            <CardContent className="space-y-4">
                {items.map((item, index) => (
                    <div key={index} className="flex flex-col md:flex-row gap-4 items-end border-b pb-4 last:border-0 last:pb-0">
                        {/* Dropdown Bahan Baku */}
                        <div className="flex-1 space-y-2 w-full">
                            <Label className="text-xs text-gray-500">Bahan Baku</Label>
                            <Select value={item.bahan_baku_id} onValueChange={(v) => updateItem(index, 'bahan_baku_id', v)}>
                                <SelectTrigger><SelectValue placeholder="Pilih Barang..." /></SelectTrigger>
                                <SelectContent>
                                    {bahanBakus.map(b => (
                                        <SelectItem key={b.id} value={b.id.toString()}>
                                            {b.nama_bahan_baku} <span className="text-gray-400 text-xs">({b.satuan?.nama_satuan}) - Sisa: {Number(b.stok_saat_ini)}</span>
                                        </SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                        </div>
                        
                        <div className="w-32 space-y-2">
                            <Label className="text-xs text-gray-500">Jumlah</Label>
                            <Input type="number" placeholder="Qty" value={item.jumlah} onChange={e => updateItem(index, 'jumlah', e.target.value)} min="0" />
                        </div>
                        
                        <div className="w-40 space-y-2">
                            <Label className="text-xs text-gray-500">Harga Beli (@)</Label>
                            <Input type="number" placeholder="Rp" value={item.harga_beli_satuan} onChange={e => updateItem(index, 'harga_beli_satuan', e.target.value)} min="0" />
                        </div>
                        
                        <div className="w-40 space-y-2 pb-2 text-right">
                            <Label className="text-xs text-gray-500 block">Subtotal</Label>
                            <span className="font-bold text-gray-900">
                                {new Intl.NumberFormat('id-ID').format(item.subtotal)}
                            </span>
                        </div>
                        
                        <Button type="button" variant="ghost" size="icon" className="text-red-500 hover:bg-red-50 mb-1" onClick={() => removeItem(index)} disabled={items.length === 1}>
                            <Trash2 className="h-4 w-4" />
                        </Button>
                    </div>
                ))}

                <div className="flex justify-end items-center gap-4 pt-6 mt-4 border-t">
                    <span className="text-lg font-medium text-gray-600">Total Faktur:</span>
                    <span className="text-3xl font-bold text-[#D93F21]">
                        {new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR' }).format(grandTotal)}
                    </span>
                </div>
            </CardContent>
        </Card>
    );
}